import logo from './logo.svg';
import React from 'react';
import {useState} from 'react';
import Listitem from './List.js'
import './App.css';
import { render } from '@testing-library/react';
import { library } from '@fortawesome/fontawesome-svg-core'
import { faTrash } from '@fortawesome/free-solid-svg-icons'
library.add(faTrash)

class App extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      todo:[],
      currentItem:{
        val:'',
        key:''
      }
    }
    this.addItem = this.addItem.bind(this);
    this.handleInput = this.handleInput.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.setUpdate=this.setUpdate.bind(this)
  }

 

  handleInput=(e)=>{
    this.setState({
      currentItem:{
        val: e.target.value,
        key: Date.now()
      }
    })
  }
  addItem(e){
    e.preventDefault();
    const newItem = this.state.currentItem;
    if(newItem.val !==""){
      const newItems = [...this.state.todo, newItem];
    this.setState({
      todo: newItems,
      currentItem:{
        val:'',
        key:''
      }
    })
    }
    console.log(this.state.todo)
  }
  deleteItem(key){
    const filteredItems= this.state.todo.filter(item =>
      item.key!==key);
    this.setState({
      todo: filteredItems
    })

  }
  setUpdate(val,key){
    // console.log("items:"+this.state.items);
    const items = this.state.todo;
    items.map(item=>{      
      if(item.key===key){
        console.log(item.key +"    "+key)
        item.val= val;
      }
    })
    this.setState({
      todo: items
    })
  }
        // setVal(e.target.value); 

        // setKey(Date.now());
      // console.log(val);
      // console.log(key);
  render(){
  return (
    <div className="main">
      
      <header>
      <h1 className="heading">Todo List</h1>
        <form id="todo-form" onSubmit={this.addItem}>

          <input type="text" placeholder="Enter Task" value={this.state.currentItem.val} onChange={this.handleInput}/>
          <button type="submit">ADD</button>
        </form>
      </header>
      <Listitem todo={this.state.todo} deleteItem={this.deleteItem} setUpdate={this.setUpdate}/>
    </div>
  );
}
}

export default App;
