import './App.css';
import {useState} from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

function Listitem(props){

    const items=props.todo;
    const lsitem=items.map(data=>{
        return <div className="list" key={data.key}>
            <p>
            <input type="text" id={data.key} value={data.val} onChange={(e)=>{
             props.setUpdate(e.target.value,data.key)}}/>
            <span>
       
        <FontAwesomeIcon className="faicons" icon="trash"  onClick={()=> { props.deleteItem(data.key)}}/>
        </span></p>
            
            </div>
    })
    return(
        <>
            <div>{lsitem}</div>
        </>
    );
}


export default Listitem;